@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
body {
  font-family: 'Roboto', sans-serif;
  margin: 0; }

.btn-circle {
  border: none;
  width: 35px;
  height: 35px;
  border-radius: 50%;
  font-size: 22px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #f1f1f1;
  -webkit-transition: all 0.32s ease-out;
  transition: all 0.32s ease-out;
  cursor: pointer; }

.counter-add-form__title {
  margin-top: 0; }

.counter-add-form__inner {
  margin: 0 auto;
  display: flex;
  align-items: center; }

.counter-add-form__group {
  margin-right: 20px;
  flex-grow: 1;
  display: flex;
  align-items: center; }
  .counter-add-form__group label {
    margin-right: 8px; }
  .counter-add-form__group input {
    border: none;
    border-bottom: 1px solid #464646;
    padding: 6px 0;
    flex-grow: 1;
    max-width: 100%; }

.counter-item {
  display: flex;
  align-items: center; }
  .counter-item__cel {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 50%; }
  .counter-item__actions {
    display: flex; }
  .counter-item__button {
    border: none;
    background-color: transparent;
    margin-right: 4px; }
    .counter-item__button:last-child {
      margin: 0; }
    .counter-item__button:last-child {
      color: #dc3545; }
    .counter-item__button:hover {
      background-color: #f0f0f0; }

.counter-filter__inner label {
  margin-right: 8px; }

.counter-filter__inner input {
  border: none;
  border-bottom: 1px solid #464646;
  padding: 6px 0; }

.counter-list {
  max-width: 620px;
  margin: 0 auto;
  border: 1px solid #f9f9f9;
  padding: 20px;
  border-radius: 7px;
  box-shadow: 0px 3px 30px 0 rgba(44, 44, 44, 0.12); }
  .counter-list__top-bar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px 0; }
    .counter-list__top-bar__title {
      margin: 0;
      display: flex;
      align-items: center; }
      .counter-list__top-bar__title h2 {
        margin-right: 20px; }
    .counter-list__top-bar__filter {
      display: flex;
      justify-content: flex-end;
      align-items: center; }
  .counter-list__toggle-filter {
    position: relative;
    display: flex; }
    .counter-list__toggle-filter__button {
      margin-left: 20px; }
    .counter-list__toggle-filter__options {
      position: absolute;
      margin: 0;
      padding: 0;
      list-style: none;
      background-color: white;
      border-radius: 7px;
      visibility: hidden;
      opacity: 0;
      -webkit-transition: all 0.32s ease-out;
      transition: all 0.32s ease-out;
      border: 1px solid #f9f9f9;
      box-shadow: 0px 3px 30px 0 rgba(44, 44, 44, 0.12); }
      .counter-list__toggle-filter__options button {
        width: 100%;
        border: none;
        background-color: white;
        white-space: nowrap;
        padding: 8px 16px; }
    .counter-list__toggle-filter--active .counter-list__toggle-filter__options {
      visibility: visible;
      opacity: 1; }
  .counter-list__header {
    display: flex; }
    .counter-list__header button {
      width: 50%;
      text-align: start;
      font-size: 16px;
      border: none;
      background-color: transparent;
      padding: 16px 0;
      font-weight: normal;
      cursor: pointer;
      display: flex;
      align-items: center; }
      .counter-list__header button.active {
        font-weight: bold; }
  .counter-list__body {
    margin: 0;
    padding: 0;
    list-style: none;
    overflow: auto;
    min-height: 200px;
    max-height: 300px; }
  .counter-list__item:nth-child(even) {
    background-color: transparent; }
  .counter-list__item:nth-child(odd) {
    background-color: #f1f1f1; }
  .counter-list__footer {
    display: flex;
    justify-content: flex-end; }
  .counter-list__total {
    width: 50%;
    text-align: start; }

@media (max-width: 767px) {
  .counter-list__top-bar {
    flex-flow: column;
    align-items: baseline; } }

.body {
  display: flex;
  min-height: 100vh;
  align-items: center; }
  .body__modal {
    background-color: white;
    border-radius: 7px;
    box-shadow: 0px 3px 30px 0 rgba(44, 44, 44, 0.12);
    padding: 20px !important; }
  .body__inner {
    width: 100%; }


